.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button {
  font-size: 18px;
  border: none;
  width: 75px;
  height: 20px;
  background-color: green;
  color: white;
  border-radius: 10px;
}

button:hover{
  background-color: white;
  color: green;
}

.button {
  border: none;
  width: 100px;
  height: 30px;
  background-color: darkred;
  color: white;
  border-radius: 10px;
  margin-top:10px ;
}

.button:hover{
  background-color: red;
  color: white;
}

